
  .footer{
    margin-top: 10px;
    position: sticky relative;
    text-align: center;
    bottom: 0;
    width: 100%;
  }


  .icons-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 30px;
    max-width: 200px;
    margin: auto;
    align-items: center;
  }

  .link-item:hover {
    border-color: rgb(240, 206, 170);
    border-color: rgb(223, 208, 240);
    border-style: solid;
    transition: all 0.3s ease 0s !important;
    border-radius: 5px;
  }

  